body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    
}
/* Hide scrollbar for Chrome, Safari and Opera */
/* width */
::-webkit-scrollbar {
    width: 0px;
    height: 0;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background:#14343f ; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #ffc500; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
/** @format Nabvar*/

* {
  margin: 0;
  font-family: "Noto Sans", sans-serif;
}
.header-navbar {
  top: 0;
  height: 64px;
  -webkit-align-items: center;
          align-items: center;
  background-color: #14343f;
  box-shadow: 1px 5px 4px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  z-index: 100;
}
/*Propiedades de los containers*/
.header-navbar .navbarDiv1 {
  display: block;
  float: left;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 33.33%;
  outline: none;
  object-fit: contain;
}
.header-navbar .navbarDiv2 {
  width: 33.33%;
  float: left;
  overflow: hidden;
  height: 64px;
 padding: auto;

}
.header-navbar .navbarDiv2 StarButton{
  display: block;
  object-fit: contain;
  
}

.header-navbar .navbarDiv3 {
  display: block;
  width: 33.33%;
  float: right;
  height: 100%;

}










/** @format */
.starInput {
  background-color: #ffc500;
  color: #fff;
  width: auto;
  height: auto;
  border-radius: 7px;
  font-size: large;
  font-family: "Press Start 2P", cursive;
  letter-spacing: 3px;
  text-shadow: 2px -1px #5c553e;
  padding: 10px;
  box-shadow: 3px 5px #c6571b;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto 
}

.starInput:active {
  color: #ffc500;
  background-color: #b08d1c;
  box-shadow: 0 2px #ffc500;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  transition-duration: 0.03s;
}
.starInput:hover{
  
}
.div-starbutton{
  display: block;
  float: right;
  width: 100%;
}


.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: auto;
  margin-right: 24px; 
  background-color:transparent;
  
}

.button-hamburgerIcon .img-hamburger{
    height: 32px;
    vertical-align: middle;
}

.button-hamburgerIcon:active{
      -webkit-transform: scale(0.90);
              transform: scale(0.90);
}


.div-menu{
  margin-top: 18px;
  background-color: #14343f;
  width: 100vw;
  margin-left: calc(-100vw + 100%);
}
.div-menu ul{
  list-style: none;
  overflow: hidden;
}

.div-menu ul li{
  color: #fff;
  margin: 0px;
  padding: 0;

}



/*Links  a*/

.div-menu ul li a{
  text-decoration: none;
  color: #fff;
  display: inline-block;
  width: 100vw;
  margin-right: calc(-100vw + 100%);
  padding: 20px 32px ;
  font-size: 20px;
  letter-spacing: 1.5px;
  
}

.div-menu ul li a:hover{
  cursor: pointer;
  -webkit-text-decoration: underline #fff;
          text-decoration: underline #fff;
  background-color:#0b1b20;
}

.div-menu ul li a:active{
  background-color: #000;
}

.div-menu ul .learn-input a:hover{
  background-color: #14343f;
}


.div-menu ul .container-component{
  display: block;
  margin-left: 32px;
}



/* Pages links*/

.div-menu ul li .suport{
  margin-top: 12px;
  float: left;
}

.div-menu ul li .donate{
  
  font-family: 'Open Sans', sans-serif;
}



/*Icons de los li*/

.div-menu ul li .icons-menu{
 
  padding-right: 12px;
  background-color: none;
  line-height: 0.5;
  text-align: center;
}
.div-menu ul li .morecontent .icons-menu{
  display: inline-block;
  padding-right: 24px ;
}





/* separador obscuro*/

.div-menu .separador{
  height: 100vw;
  opacity: 0.5;
  background: #000;
 
}

