/*Button*/
.learn-code-input{
    
    cursor: pointer;
    outline: none;
    display: block;
   
     /*Size*/
    width:auto;
    height: auto;
    
   /*Colors */
    background-color:#4ea6a6 ;
    color:#fff ;
    text-shadow: 2px -1px #5c553e;
    box-shadow: 4px -3px #2e4a58;

    /*Margin*/
    margin: 0;

    /*Padding*/
    padding: 15px 10px;

    /*TEXT*/
    font-size: 20px;
    font-family: "Press Start 2P";
    letter-spacing: 0px; 

    /*Border*/
    border-radius: 5px;
    border: none;

    
  }

  .learn-code-input:active{
    color: #00aaff;
    background-color: violet;
    box-shadow: 4px -3px #00aaff;
    transform: translateY(-4px);
    transition-duration: 0.03s;
  }
  .div-menu ul .li-learn:hover{
    text-decoration: underline #fff 3.5px;
  }